<template>
  <div class="chunk-uploader">
    <!-- ‰∏ä‰º†ÊåâÈíÆ -->
    <div class="upload-button-container">
      <input
        type="file"
        ref="fileInput"
        @change="handleFileChange"
        multiple
        class="file-input"
      />
      <button @click="triggerFileInput" class="upload-button">
        ÈÄâÊã©Êñá‰ª∂‰∏ä‰º†
      </button>
    </div>

    <!-- ‰∏ä‰º†ËøõÂ∫¶ÂØπËØùÊ°Ü -->
    <div class="upload-dialog" v-if="showUploadDialog">
      <div class="upload-dialog-header">
        <h3>Êñá‰ª∂‰∏ä‰º†ËøõÂ∫¶</h3>
        <button @click="closeDialog" class="close-button" :disabled="isUploading">√ó</button>
      </div>
      <div class="upload-dialog-body">
        <div v-for="(file, index) in uploadTasks" :key="index" class="file-item">
          <div class="file-info">
            <div class="file-name">{{ file.name }}</div>
            <div class="file-size">{{ formatFileSize(file.size) }}</div>
            <div class="file-status">
              {{ getFileStatus(file) }}
              <span v-if="file.error" class="error-message">: {{ file.error }}</span>
            </div>
          </div>
          <div class="progress-container">
            <div class="progress-bar" :style="{ width: file.progress + '%' }"></div>
            <div class="progress-text">{{ file.progress.toFixed(1) }}%</div>
          </div>
        </div>
      </div>
      <div class="upload-dialog-footer">
        <button 
          @click="startUpload" 
          :disabled="isUploading || uploadTasks.length === 0 || allTasksCompleted" 
          class="upload-start-button"
        >
          ÂºÄÂßã‰∏ä‰º†
        </button>
        <button 
          @click="closeDialog" 
          :disabled="isUploading" 
          class="close-button"
        >
          ÂÖ≥Èó≠
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { initChunk, uploadChunk, completeChunk } from '../api/events';
import SparkMD5 from 'spark-md5';

export default {
  name: 'ChunkUploader',
  props: {
    // ÊúÄÂ§ßÂπ∂Ë°å‰∏ä‰º†Êñá‰ª∂Êï∞
    maxParallelFiles: {
      type: Number,
      default: 3
    },
    // ÊØè‰∏™Êñá‰ª∂ÊúÄÂ§ßÂπ∂Ë°åÂàÜÁâáÊï∞
    maxParallelChunks: {
      type: Number,
      default: 3
    },
    // ÊØè‰∏™ÂàÜÁâáÂ§ßÂ∞è(bytes) - ÈªòËÆ§ 2MB
    chunkSize: {
      type: Number,
      default: 2 * 1024 * 1024
    }
  },
  data() {
    return {
      showUploadDialog: false,
      uploadTasks: [],
      activeUploads: 0,
      isUploading: false
    };
  },
  computed: {
    allTasksCompleted() {
      return this.uploadTasks.length > 0 && 
             this.uploadTasks.every(task => task.status === 'completed' || task.status === 'error');
    }
  },
  methods: {
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    
    handleFileChange(event) {
      const files = event.target.files;
      if (!files || files.length === 0) return;
      
      // Ê∑ªÂä†Êñá‰ª∂Âà∞‰ªªÂä°ÂàóË°®
      Array.from(files).forEach(file => {
        this.addFile(file);
      });
      
      // Ê∏ÖÁ©∫Êñá‰ª∂ËæìÂÖ•ÔºåÂÖÅËÆ∏ÈáçÊñ∞ÈÄâÊã©Áõ∏ÂêåÁöÑÊñá‰ª∂
      this.$refs.fileInput.value = '';
    },
    
    // Ê∑ªÂä†Âçï‰∏™Êñá‰ª∂Âà∞‰∏ä‰º†ÈòüÂàó
    addFile(file) {
      // ÂàõÂª∫Êñ∞‰ªªÂä°
      const newTask = {
        file,
        name: file.name,
        size: file.size,
        progress: 0,
        status: 'pending', // pending, calculating, uploading, completed, error
        error: null,
        taskId: null,
        totalChunks: Math.ceil(file.size / this.chunkSize),
        uploadedChunks: 0,
        chunkQueue: [],
        md5: null
      };
      
      // Ê∑ªÂä†Âà∞‰ªªÂä°ÂàóË°®
      this.uploadTasks.push(newTask);
      this.showUploadDialog = true;
      
      return newTask;
    },
    
    closeDialog() {
      if (this.isUploading) return;
      
      // ÂÖ≥Èó≠ÂØπËØùÊ°ÜÂπ∂Ê∏ÖÁ©∫Â∑≤ÂÆåÊàêÊàñÈîôËØØÁöÑ‰ªªÂä°
      this.showUploadDialog = false;
      this.uploadTasks = this.uploadTasks.filter(
        task => task.status !== 'completed' && task.status !== 'error'
      );
    },
    
    formatFileSize(bytes) {
      if (bytes < 1024) return bytes + ' B';
      else if (bytes < 1024 * 1024) return (bytes / 1024).toFixed(2) + ' KB';
      else if (bytes < 1024 * 1024 * 1024) return (bytes / (1024 * 1024)).toFixed(2) + ' MB';
      else return (bytes / (1024 * 1024 * 1024)).toFixed(2) + ' GB';
    },
    
    getFileStatus(file) {
      switch (file.status) {
        case 'pending': return 'Á≠âÂæÖ‰∏ä‰º†';
        case 'calculating': return 'ËÆ°ÁÆóMD5...';
        case 'uploading': return `‰∏ä‰º†‰∏≠ (${file.uploadedChunks}/${file.totalChunks})`;
        case 'verifying': return 'È™åËØÅ‰∏≠...';
        case 'completed': return 'ÂÆåÊàê';
        case 'error': return 'ÈîôËØØ';
        default: return file.status;
      }
    },
    
    async startUpload() {
      if (this.isUploading) return;
      this.isUploading = true;
      
      // ËøáÊª§Âá∫ÈúÄË¶Å‰∏ä‰º†ÁöÑ‰ªªÂä°
      const pendingTasks = this.uploadTasks.filter(task => 
        task.status === 'pending' || (task.status === 'error' && !task.taskId)
      );
      
      // ÂºÄÂßãËÆ°ÁÆóMD5
      for (const task of pendingTasks) {
        this.calculateFileMD5(task);
      }
    },
    
    async calculateFileMD5(task) {
      task.status = 'calculating';
      
      try {
        const md5 = await this.computeFileMD5(task.file);
        task.md5 = md5;
        this.prepareFileUpload(task);
      } catch (error) {
        console.error('MD5 calculation error:', error);
        task.status = 'error';
        task.error = 'MD5ËÆ°ÁÆóÂ§±Ë¥•';
        this.checkAllTasksCompleted();
      }
    },
    
    async computeFileMD5(file) {
      return new Promise((resolve, reject) => {
        const blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
        const chunkSize = 2097152; // 2MB per chunk for MD5 calculation
        const chunks = Math.ceil(file.size / chunkSize);
        let currentChunk = 0;
        const spark = new SparkMD5.ArrayBuffer();
        const fileReader = new FileReader();
        
        fileReader.onload = (e) => {
          spark.append(e.target.result); // Append array buffer
          currentChunk++;
          
          if (currentChunk < chunks) {
            loadNext();
          } else {
            resolve(spark.end()); // Return the MD5 hash
          }
        };
        
        fileReader.onerror = (error) => {
          reject(error);
        };
        
        const loadNext = () => {
          const start = currentChunk * chunkSize;
          const end = Math.min(file.size, start + chunkSize);
          fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
        };
        
        loadNext();
      });
    },
    
    async prepareFileUpload(task) {
      try {
        // ÂáÜÂ§áÂàÜÂùó‰∏ä‰º†ËØ∑Ê±Ç
        const initRequest = {
          fileName: task.name,
          fileType: task.file.type,
          fileSize: task.file.size,
          chunkCount: task.totalChunks,
          fileMD5: task.md5
        };
        
        // ÂàùÂßãÂåñÂàÜÂùó‰∏ä‰º†
        const response = await initChunk(initRequest);
        
        if (response.success && response.data) {
          task.taskId = response.data.taskId;
          task.status = 'uploading';
          this.createChunkQueue(task);
          this.processUploadQueue();
        } else {
          task.status = 'error';
          task.error = response.message || 'ÂàùÂßãÂåñ‰∏ä‰º†Â§±Ë¥•';
          this.checkAllTasksCompleted();
        }
      } catch (error) {
        console.error('Upload preparation error:', error);
        task.status = 'error';
        task.error = 'ÂáÜÂ§á‰∏ä‰º†Â§±Ë¥•';
        this.checkAllTasksCompleted();
      }
    },
    
    createChunkQueue(task) {
      const { file } = task;
      const { chunkSize } = this;
      task.chunkQueue = [];
      
      for (let i = 0; i < task.totalChunks; i++) {
        const start = i * chunkSize;
        const end = Math.min(file.size, start + chunkSize);
        const chunkFile = file.slice(start, end);
        
        task.chunkQueue.push({
          index: i,
          file: chunkFile,
          uploaded: false,
          uploading: false,
          retries: 0
        });
      }
    },
    
    async processUploadQueue() {
      // ËÆ°ÁÆóÂΩìÂâçÊ≠£Âú®‰∏ä‰º†ÁöÑÊñá‰ª∂Êï∞
      this.activeUploads = this.uploadTasks.filter(task => 
        task.status === 'uploading' && task.chunkQueue.some(chunk => chunk.uploading)
      ).length;
      
      // Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•ÂºÄÂßãÊñ∞ÁöÑÊñá‰ª∂‰∏ä‰º†
      for (const task of this.uploadTasks) {
        if (task.status === 'uploading' && !task.chunkQueue.some(chunk => chunk.uploading)) {
          if (this.activeUploads < this.maxParallelFiles) {
            this.uploadFileChunks(task);
            this.activeUploads++;
          }
        }
      }
    },
    
    async uploadFileChunks(task) {
      // ËÆ°ÁÆóÂΩìÂâçÊ¥ªÂä®ÁöÑÂàÜÁâá‰∏ä‰º†Êï∞Èáè
      const activeChunks = task.chunkQueue.filter(chunk => chunk.uploading).length;
      
      // Â¶ÇÊûúÊâÄÊúâÂàÜÁâáÂ∑≤‰∏ä‰º†ÔºåÂÆåÊàê‰∏ä‰º†
      if (task.chunkQueue.every(chunk => chunk.uploaded)) {
        await this.completeFileUpload(task);
        return;
      }
      
      // ‰∏ä‰º†Êõ¥Â§öÂàÜÁâáÔºåÁõ¥Âà∞ËææÂà∞Âπ∂Ë°åÈôêÂà∂
      for (let i = 0; i < task.chunkQueue.length && activeChunks < this.maxParallelChunks; i++) {
        const chunk = task.chunkQueue[i];
        if (!chunk.uploaded && !chunk.uploading) {
          chunk.uploading = true;
          this.uploadChunk(task, chunk);
        }
      }
    },
    
    async uploadChunk(task, chunk) {
      try {
        // ‰ΩøÁî® API ‰∏ä‰º†ÂàÜÁâá
        const response = await uploadChunk(task.taskId, chunk.index, chunk.file);
        
        if (response.success) {
          chunk.uploaded = true;
          chunk.uploading = false;
          task.uploadedChunks++;
          
          // Êõ¥Êñ∞ËøõÂ∫¶
          task.progress = (task.uploadedChunks / task.totalChunks) * 100;
          
          // ÁªßÁª≠‰∏ä‰º†Êõ¥Â§öÂàÜÁâá
          this.uploadFileChunks(task);
        } else {
          // ÈáçËØïÈÄªËæë
          if (chunk.retries < 3) {
            chunk.retries++;
            chunk.uploading = false;
            // Á®çÂêéÈáçËØï
            setTimeout(() => {
              this.uploadChunk(task, chunk);
            }, 1000);
          } else {
            chunk.uploading = false;
            task.status = 'error';
            task.error = response.message || `ÂàÜÁâá ${chunk.index + 1} ‰∏ä‰º†Â§±Ë¥•`;
            this.checkAllTasksCompleted();
          }
        }
      } catch (error) {
        console.error('Chunk upload error:', error);
        // ÈáçËØïÈÄªËæëÂêå‰∏ä
        if (chunk.retries < 3) {
          chunk.retries++;
          chunk.uploading = false;
          setTimeout(() => {
            this.uploadChunk(task, chunk);
          }, 1000);
        } else {
          chunk.uploading = false;
          task.status = 'error';
          task.error = `ÂàÜÁâá ${chunk.index + 1} ‰∏ä‰º†Â§±Ë¥•`;
          this.checkAllTasksCompleted();
        }
      }
      
      // Ê£ÄÊü•ÂÖ∂‰ªñ‰ªªÂä°ÁöÑ‰∏ä‰º†
      this.processUploadQueue();
    },
    
    async completeFileUpload(task) {
      task.status = 'verifying';
      
      try {
        const response = await completeChunk(task.taskId);
        
        if (response.success) {
          // Ê£ÄÊü•MD5ÊòØÂê¶ÂåπÈÖç
          if (response.data.md5Verified) {
            task.status = 'completed';
            this.$emit('upload-complete', {
              fileName: response.data.originalName,
              serverFileName: response.data.serverFileName,
              size: response.data.size,
              md5: response.data.md5
            });
          } else {
            task.status = 'error';
            task.error = `MD5Ê†°È™åÂ§±Ë¥•ÔºåÊúüÊúõ: ${response.data.expectedMD5}ÔºåÂÆûÈôÖ: ${response.data.md5}`;
          }
        } else {
          task.status = 'error';
          task.error = response.message || 'ÂÆåÊàê‰∏ä‰º†Â§±Ë¥•';
        }
      } catch (error) {
        console.error('Complete upload error:', error);
        task.status = 'error';
        task.error = 'ÂÆåÊàê‰∏ä‰º†Â§±Ë¥•';
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊâÄÊúâ‰ªªÂä°ÈÉΩÂ∑≤ÂÆåÊàê
      this.checkAllTasksCompleted();
      
      // Â§ÑÁêÜ‰∏ã‰∏Ä‰∏™Êñá‰ª∂‰∏ä‰º†
      this.processUploadQueue();
    },
    
    checkAllTasksCompleted() {
      // Ê£ÄÊü•ÊâÄÊúâ‰ªªÂä°ÊòØÂê¶ÂÆåÊàê
      const isAllCompleted = this.uploadTasks.every(
        task => task.status === 'completed' || task.status === 'error'
      );
      
      if (isAllCompleted) {
        this.isUploading = false;
        this.$emit('all-completed');
      }
    }
  }
};
</script>

<style scoped>
.chunk-uploader {
  display: flex;
  flex-direction: column;
}

.file-input {
  display: none;
}

.upload-button-container {
  margin-bottom: 1rem;
}

.upload-button {
  padding: 8px 16px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.upload-button:hover {
  background-color: #45a049;
}

.upload-dialog {
  position: fixed;
  z-index: 1000;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  max-width: 90vw;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
}

.upload-dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid #eee;
}

.upload-dialog-header h3 {
  margin: 0;
  font-size: 18px;
}

.upload-dialog-body {
  padding: 16px;
  max-height: 400px;
  overflow-y: auto;
}

.upload-dialog-footer {
  padding: 12px 16px;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.file-item {
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #eee;
}

.file-item:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.file-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  flex-wrap: wrap;
}

.file-name {
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 50%;
}

.file-size {
  color: #666;
}

.file-status {
  width: 100%;
  margin-top: 4px;
  color: #555;
}

.error-message {
  color: #f44336;
}

.progress-container {
  height: 20px;
  background-color: #f5f5f5;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background-color: #4caf50;
  transition: width 0.3s;
}

.progress-text {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
  font-size: 12px;
  font-weight: bold;
}

.close-button {
  padding: 5px 10px;
  background-color: #f1f1f1;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.close-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.upload-start-button {
  padding: 8px 16px;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.upload-start-button:hover:not(:disabled) {
  background-color: #0b7dda;
}

.upload-start-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
</style>
