<template>
  <div class="video-player-page">
    <!-- ËøîÂõûÊåâÈíÆ -->
    <header class="player-header">
      <button class="back-btn" @click="goBack">
        ‚Üê ËøîÂõû
      </button>
      <h1 class="page-title">ËßÜÈ¢ëÊí≠Êîæ</h1>
    </header>

    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <div v-if="loading" class="loading">
      <div class="loading-spinner">‚è≥</div>
      <p>Ê≠£Âú®Âä†ËΩΩËßÜÈ¢ë...</p>
    </div>

    <!-- ÈîôËØØÁä∂ÊÄÅ -->
    <div v-else-if="error" class="error-container">
      <div class="error-icon">‚ö†Ô∏è</div>
      <p>{{ error }}</p>
      <button class="retry-btn" @click="loadVideoData">ÈáçËØï</button>
    </div>

    <!-- ËßÜÈ¢ëÊí≠ÊîæÂô® -->
    <div v-else-if="event && currentVideo" class="video-container">
      <div class="player-wrapper">
        <!-- Ë∞ÉËØï‰ø°ÊÅØÈù¢Êùø -->
        <div v-if="showDebugLog" class="debug-panel">
          <div class="debug-header">
            <h4>Ë∞ÉËØïÊó•Âøó</h4>
            <button @click="clearLog" class="clear-log-btn">Ê∏ÖÁ©∫</button>
            <button @click="showDebugLog = false" class="close-debug-btn">√ó</button>
          </div>
          <div class="debug-content" ref="debugContent">
            <div 
              v-for="(log, index) in debugLogs" 
              :key="index"
              :class="['log-entry', `log-${log.type}`]"
            >
              <span class="log-time">{{ log.time }}</span>
              <span class="log-message">{{ log.message }}</span>
            </div>
          </div>
        </div>

        <!-- Ë∞ÉËØïÊéßÂà∂ÊåâÈíÆ -->
        <div class="debug-controls">
          <button 
            @click="showDebugLog = !showDebugLog" 
            class="debug-toggle-btn"
            :class="{ active: showDebugLog }"
          >
            üêõ Ë∞ÉËØïÊó•Âøó ({{ debugLogs.length }})
          </button>
          <button @click="testVideoUrl" class="test-url-btn">ÊµãËØïËßÜÈ¢ëURL</button>
          <button @click="checkDOMState" class="test-url-btn">Ê£ÄÊü•DOMÁä∂ÊÄÅ</button>
        </div>

        <div 
          ref="videoContainer" 
          class="xgplayer-container"
          v-if="currentVideo"
        >
          <div
            :id="`xg-player-${event.id}-${currentVideoIndex}`"
            ref="videoPlayer"
            :key="`video-${event.id}-${currentVideoIndex}`"
          ></div>
        </div>
        
        <!-- ËßÜÈ¢ë‰ø°ÊÅØ -->
        <div class="video-info">
          <h2 class="video-title">{{ currentVideo.desc || 'ËßÜÈ¢ë' }}</h2>
          <div class="video-meta">
            <span class="event-title">Êù•Ëá™: {{ event.title }}</span>
            <span class="video-date">{{ formatDate(event.date) }}</span>
          </div>
        </div>

        <!-- ËßÜÈ¢ëÂàóË°® -->
        <div v-if="videoList.length > 1" class="video-list">
          <h3 class="list-title">ËßÜÈ¢ëÂàóË°® ({{ videoList.length }}‰∏™)</h3>
          <div class="video-thumbnails">
            <div 
              v-for="(video, index) in videoList" 
              :key="index"
              class="thumbnail-item"
              :class="{ active: index === currentVideoIndex }"
              @click="switchVideo(index)"
            >
              <div 
                class="thumbnail-preview"
                :style="{ backgroundImage: `url(${getMediaUrl(event.id, video.fileName, true)})` }"
              >
                <span class="play-overlay">‚ñ∂Ô∏è</span>
                <span v-if="video.duration" class="duration">{{ formatDuration(video.duration) }}</span>
              </div>
              <p class="thumbnail-desc">{{ video.desc || `ËßÜÈ¢ë${index + 1}` }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount, nextTick, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import dayjs from 'dayjs'
import Player from 'xgplayer'
import { getEventById, getVideoUrl, getMediaUrl } from '@/api/events'
import { loadConfig } from '@/config'

export default {
  name: 'VideoPlayer',
  setup() {
    const route = useRoute()
    const router = useRouter()
    
    const event = ref(null)
    const currentVideo = ref(null)
    const currentVideoIndex = ref(0)
    const videoList = ref([])
    const loading = ref(true)
    const error = ref('')
    
    const videoPlayer = ref(null)
    const videoContainer = ref(null)
    const debugContent = ref(null)
    let player = null

    // Ë∞ÉËØïÁõ∏ÂÖ≥Áä∂ÊÄÅ
    const showDebugLog = ref(false)
    const debugLogs = ref([])

    // Ê∑ªÂä†Ë∞ÉËØïÊó•Âøó
    const addLog = (message, type = 'info') => {
      const log = {
        time: dayjs().format('HH:mm:ss.SSS'),
        message,
        type
      }
      debugLogs.value.push(log)
      console.log(`[VideoPlayer ${type}]:`, message)
      
      // ÊªöÂä®Âà∞Â∫ïÈÉ®
      nextTick(() => {
        if (debugContent.value) {
          debugContent.value.scrollTop = debugContent.value.scrollHeight
        }
      })
    }

    // Ê∏ÖÁ©∫Êó•Âøó
    const clearLog = () => {
      debugLogs.value = []
    }

    // Á≠âÂæÖDOMÂÖÉÁ¥†ÂèØÁî®
    const waitForElement = (refElement, maxWait = 3000) => {
      return new Promise((resolve, reject) => {
        const startTime = Date.now()
        
        const check = () => {
          if (refElement.value) {
            addLog('DOMÂÖÉÁ¥†Â∑≤Â∞±Áª™', 'success')
            resolve(refElement.value)
            return
          }
          
          if (Date.now() - startTime > maxWait) {
            addLog(`Á≠âÂæÖDOMÂÖÉÁ¥†Ë∂ÖÊó∂ (${maxWait}ms)`, 'error')
            reject(new Error('Á≠âÂæÖDOMÂÖÉÁ¥†Ë∂ÖÊó∂'))
            return
          }
          
          setTimeout(check, 50)
        }
        
        check()
      })
    }

    const xgPlayerOptions = ref({
      fluid: true,
      fitVideoSize: 'fixWidth',
      width: '100%',
      height: 'auto',
      playbackRate: [0.5, 1, 1.25, 1.5, 2],
      controls: true,
      preload: 'auto',
      playsinline: true,
      autoplay: false,
      lang: 'zh-cn'
    })

    // Âä†ËΩΩËßÜÈ¢ëÊï∞ÊçÆ
    const loadVideoData = async () => {
      try {
        loading.value = true
        error.value = ''
        addLog('ÂºÄÂßãÂä†ËΩΩËßÜÈ¢ëÊï∞ÊçÆ', 'info')
        
        const eventId = route.params.id
        const videoIndex = parseInt(route.params.videoIndex) || 0
        addLog(`Ë∑ØÁî±ÂèÇÊï∞ - eventId: ${eventId}, videoIndex: ${videoIndex}`, 'info')

        // Ê£ÄÊü•ÈÖçÁΩÆÂä†ËΩΩ
        try {
          await loadConfig()
          addLog('ÈÖçÁΩÆÊñá‰ª∂Âä†ËΩΩÊàêÂäü', 'success')
        } catch (configErr) {
          addLog(`ÈÖçÁΩÆÊñá‰ª∂Âä†ËΩΩÂ§±Ë¥•: ${configErr.message}`, 'error')
          error.value = 'ÈÖçÁΩÆÊñá‰ª∂Âä†ËΩΩÂ§±Ë¥•'
          return
        }
        
        // Ëé∑Âèñ‰∫ã‰ª∂ËØ¶ÊÉÖ
        addLog('Ê≠£Âú®Ëé∑Âèñ‰∫ã‰ª∂ËØ¶ÊÉÖ...', 'info')
        const response = await getEventById(eventId)
        if (!response.success) {
          addLog(`Ëé∑Âèñ‰∫ã‰ª∂ËØ¶ÊÉÖÂ§±Ë¥•: ${response.message}`, 'error')
          error.value = response.message || 'Ëé∑Âèñ‰∫ã‰ª∂ËØ¶ÊÉÖÂ§±Ë¥•'
          return
        }
        
        event.value = response.data
        videoList.value = event.value.media?.videos || []
        addLog(`‰∫ã‰ª∂ËØ¶ÊÉÖËé∑ÂèñÊàêÂäü, ÊâæÂà∞ ${videoList.value.length} ‰∏™ËßÜÈ¢ë`, 'success')
        
        if (videoList.value.length === 0) {
          addLog('ËØ•‰∫ã‰ª∂Ê≤°ÊúâËßÜÈ¢ëÊñá‰ª∂', 'warning')
          error.value = 'ËØ•‰∫ã‰ª∂Ê≤°ÊúâËßÜÈ¢ë'
          return
        }
        
        // ËÆæÁΩÆÂΩìÂâçËßÜÈ¢ë
        currentVideoIndex.value = Math.min(videoIndex, videoList.value.length - 1)
        currentVideo.value = videoList.value[currentVideoIndex.value]
        addLog(`ÂΩìÂâçËßÜÈ¢ë: ${currentVideo.value.fileName} (Á¥¢Âºï: ${currentVideoIndex.value})`, 'info')
        
        // DOM Êõ¥Êñ∞ÂêéÔºåwatch ‰ºöËá™Âä®Ë∞ÉÁî® initializePlayer
        addLog('Á≠âÂæÖDOMÊõ¥Êñ∞ÂíåÊí≠ÊîæÂô®ÂàùÂßãÂåñ...', 'info')
        
      } catch (err) {
        addLog(`Âä†ËΩΩËßÜÈ¢ëÊï∞ÊçÆÂ§±Ë¥•: ${err.message}`, 'error')
        error.value = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï'
        console.error('Âä†ËΩΩËßÜÈ¢ëÊï∞ÊçÆÂ§±Ë¥•:', err)
      } finally {
        loading.value = false
      }
    }



    // ÂàùÂßãÂåñË•øÁìúÊí≠ÊîæÂô®
    const initializePlayer = async () => {
      addLog('ÂºÄÂßãÂàùÂßãÂåñË•øÁìúÊí≠ÊîæÂô®...', 'info')
      
      if (player) {
        addLog('Ê∏ÖÁêÜÊóßÁöÑÊí≠ÊîæÂô®ÂÆû‰æã', 'info')
        player.destroy()
        player = null
      }

      try {
        // Á≠âÂæÖÂÆπÂô®ÂÖÉÁ¥†ÂèØÁî®
        addLog('Á≠âÂæÖÂÆπÂô®ÂÖÉÁ¥†ÂèØÁî®...', 'info')
        await waitForElement(videoPlayer, 3000)
        
        addLog(`ÂÆπÂô®ÂÖÉÁ¥†Â∑≤ÊâæÂà∞: ${videoPlayer.value.tagName}`, 'success')
        addLog(`ÂÆπÂô®ÂÖÉÁ¥†ID: ${videoPlayer.value.id}`, 'info')
      } catch (err) {
        addLog(`Á≠âÂæÖÂÆπÂô®ÂÖÉÁ¥†Â§±Ë¥•: ${err.message}`, 'error')
        error.value = 'ËßÜÈ¢ëÊí≠ÊîæÂô®ÂàùÂßãÂåñÂ§±Ë¥•ÔºöÂÆπÂô®ÂÖÉÁ¥†Êú™ÊâæÂà∞'
        return
      }

      try {
        const videoUrl = getVideoUrl(event.value.id, currentVideo.value.fileName)
        addLog(`ÁîüÊàêÁöÑËßÜÈ¢ëURL: ${videoUrl}`, 'info')

        // ÈÖçÁΩÆË•øÁìúÊí≠ÊîæÂô®ÈÄâÈ°π
        const playerConfig = {
          ...xgPlayerOptions.value,
          id: videoPlayer.value.id,
          url: videoUrl,
          el: videoPlayer.value
        }

        addLog('Ê≠£Âú®ÂàùÂßãÂåñË•øÁìúÊí≠ÊîæÂô®...', 'info')
        addLog(`Êí≠ÊîæÂô®ÈÖçÁΩÆ: ${JSON.stringify(playerConfig, null, 2)}`, 'info')
        
        player = new Player(playerConfig)
        
        addLog('Ë•øÁìúÊí≠ÊîæÂô®ÂàùÂßãÂåñÊàêÂäü', 'success')
        addLog(`Êí≠ÊîæÂô®ÂÆû‰æã: ${typeof player}`, 'info')
        console.log('XGPlayer initialized', player)

        // Ê∑ªÂä†‰∫ã‰ª∂ÁõëÂê¨
        player.on('ready', () => {
          addLog('Êí≠ÊîæÂô®ÂáÜÂ§áÂ∞±Áª™', 'success')
        })

        player.on('loadstart', () => {
          addLog('ÂºÄÂßãÂä†ËΩΩËßÜÈ¢ë', 'info')
        })

        player.on('loadeddata', () => {
          addLog('ËßÜÈ¢ëÊï∞ÊçÆÂ∑≤Âä†ËΩΩ', 'success')
        })
        
        player.on('loadedmetadata', () => {
          addLog('ËßÜÈ¢ëÂÖÉÊï∞ÊçÆÂ∑≤Âä†ËΩΩ', 'success')
          try {
            const duration = player.duration
            addLog(`ËßÜÈ¢ëÊó∂Èïø: ${duration}Áßí`, 'info')
            const videoWidth = player.video?.videoWidth || 0
            const videoHeight = player.video?.videoHeight || 0
            addLog(`ËßÜÈ¢ëÂ∞∫ÂØ∏: ${videoWidth}x${videoHeight}`, 'info')
          } catch (e) {
            addLog(`Ëé∑ÂèñËßÜÈ¢ë‰ø°ÊÅØÂ§±Ë¥•: ${e.message}`, 'warning')
          }
        })

        player.on('canplay', () => {
          addLog('ËßÜÈ¢ëÂèØ‰ª•ÂºÄÂßãÊí≠Êîæ', 'success')
        })

        player.on('canplaythrough', () => {
          addLog('ËßÜÈ¢ëÂ∑≤ÁºìÂÜ≤Ë∂≥Â§üÔºåÂèØ‰ª•ÊµÅÁïÖÊí≠Êîæ', 'success')
        })

        player.on('play', () => {
          addLog('ËßÜÈ¢ëÂºÄÂßãÊí≠Êîæ', 'success')
        })

        player.on('pause', () => {
          addLog('ËßÜÈ¢ëÊöÇÂÅú', 'info')
        })

        player.on('waiting', () => {
          addLog('ËßÜÈ¢ëÁºìÂÜ≤‰∏≠...', 'warning')
        })

        player.on('stalled', () => {
          addLog('ËßÜÈ¢ëÂä†ËΩΩÂÅúÊªû', 'warning')
        })

        player.on('emptied', () => {
          addLog('ËßÜÈ¢ëÂÖÉÁ¥†Â∑≤Ê∏ÖÁ©∫', 'warning')
        })

        player.on('abort', () => {
          addLog('ËßÜÈ¢ëÂä†ËΩΩË¢´‰∏≠Ê≠¢', 'warning')
        })

        player.on('suspend', () => {
          addLog('ËßÜÈ¢ëÂä†ËΩΩÊöÇÂÅú', 'warning')
        })
        
        player.on('error', (e) => {
          addLog(`Ë•øÁìúÊí≠ÊîæÂô®ÈîôËØØ: ${JSON.stringify(e)}`, 'error')
          console.error('XGPlayer error:', e)
          
          // Â∞ùËØïËé∑ÂèñÊõ¥ËØ¶ÁªÜÁöÑÈîôËØØ‰ø°ÊÅØ
          if (player.video) {
            const videoError = player.video.error
            if (videoError) {
              addLog(`ËßÜÈ¢ëÂÖÉÁ¥†ÈîôËØØÁ†Å: ${videoError.code}`, 'error')
              addLog(`ËßÜÈ¢ëÂÖÉÁ¥†ÈîôËØØ‰ø°ÊÅØ: ${videoError.message}`, 'error')
            }
          }
          
          error.value = `ËßÜÈ¢ëÊí≠ÊîæÂ§±Ë¥•: ${e.message || JSON.stringify(e) || 'Êú™Áü•ÈîôËØØ'}`
        })
        
        player.on('ended', () => {
          addLog('ËßÜÈ¢ëÊí≠ÊîæÁªìÊùü', 'info')
          console.log('Video ended')
          // Ëá™Âä®Êí≠Êîæ‰∏ã‰∏Ä‰∏™ËßÜÈ¢ë
          if (currentVideoIndex.value < videoList.value.length - 1) {
            switchVideo(currentVideoIndex.value + 1)
          }
        })

        // Ê∑ªÂä†Âª∂ËøüÊ£ÄÊü•ÔºåÁ°Æ‰øùÊí≠ÊîæÂô®Ê≠£Á°ÆÊ∏≤Êüì
        setTimeout(() => {
          addLog('Âª∂ËøüÊ£ÄÊü•Êí≠ÊîæÂô®Áä∂ÊÄÅ...', 'info')
          const videoElement = player.video
          if (videoElement) {
            addLog(`ËßÜÈ¢ëÂÖÉÁ¥†Â≠òÂú®: ${videoElement.tagName}`, 'success')
            addLog(`ËßÜÈ¢ëÂÖÉÁ¥†src: ${videoElement.src}`, 'info')
            addLog(`ËßÜÈ¢ëÂÖÉÁ¥†readyState: ${videoElement.readyState}`, 'info')
            addLog(`ËßÜÈ¢ëÂÖÉÁ¥†networkState: ${videoElement.networkState}`, 'info')
          } else {
            addLog('Êú™ÊâæÂà∞ËßÜÈ¢ëÂÖÉÁ¥†', 'error')
          }
          
          const playerElement = document.querySelector(`#${playerConfig.id}`)
          if (playerElement) {
            addLog(`Êí≠ÊîæÂô®ÂÆπÂô®Â≠òÂú®ÔºåÂ≠êÂÖÉÁ¥†Êï∞Èáè: ${playerElement.children.length}`, 'info')
            addLog(`Êí≠ÊîæÂô®ÂÆπÂô®ÂÜÖÂÆπ: ${playerElement.innerHTML.substring(0, 200)}...`, 'info')
          } else {
            addLog('Êú™ÊâæÂà∞Êí≠ÊîæÂô®ÂÆπÂô®', 'error')
          }
        }, 1000)

      } catch (err) {
        addLog(`Ë•øÁìúÊí≠ÊîæÂô®ÂàùÂßãÂåñÂ§±Ë¥•: ${err.message}`, 'error')
        console.error('Failed to initialize XGPlayer:', err)
        error.value = 'ËßÜÈ¢ëÊí≠ÊîæÂô®ÂàùÂßãÂåñÂ§±Ë¥•'
      }
    }

    // Ê£ÄÊü•DOMÁä∂ÊÄÅ
    const checkDOMState = () => {
      addLog('=== DOMÁä∂ÊÄÅÊ£ÄÊü• ===', 'info')
      addLog(`videoPlayer.value: ${!!videoPlayer.value}`, 'info')
      addLog(`videoContainer.value: ${!!videoContainer.value}`, 'info')
      addLog(`currentVideo: ${!!currentVideo.value}`, 'info')
      addLog(`event: ${!!event.value}`, 'info')
      
      if (videoPlayer.value) {
        addLog(`ËßÜÈ¢ëÂÖÉÁ¥†ID: ${videoPlayer.value.id || 'Êó†'}`, 'info')
        addLog(`ËßÜÈ¢ëÂÖÉÁ¥†Á±ªÂêç: ${videoPlayer.value.className}`, 'info')
        addLog(`ËßÜÈ¢ëÂÖÉÁ¥†ÊòØÂê¶Âú®DOM‰∏≠: ${document.contains(videoPlayer.value)}`, 'info')
        addLog(`ËßÜÈ¢ëÂÖÉÁ¥†ÂÆΩÂ∫¶: ${videoPlayer.value.offsetWidth}`, 'info')
        addLog(`ËßÜÈ¢ëÂÖÉÁ¥†È´òÂ∫¶: ${videoPlayer.value.offsetHeight}`, 'info')
      }
      
      const allVideos = document.querySelectorAll('video')
      addLog(`È°µÈù¢‰∏≠videoÂÖÉÁ¥†ÊÄªÊï∞: ${allVideos.length}`, 'info')
      
      const vjsElements = document.querySelectorAll('.video-js')
      addLog(`È°µÈù¢‰∏≠.video-jsÂÖÉÁ¥†ÊÄªÊï∞: ${vjsElements.length}`, 'info')
      
      if (player) {
        addLog(`Video.jsÊí≠ÊîæÂô®Áä∂ÊÄÅ: Â∑≤ÂàùÂßãÂåñ`, 'success')
        try {
          addLog(`Êí≠ÊîæÂô®Â∞±Áª™Áä∂ÊÄÅ: ${player.readyState()}`, 'info')
        } catch (e) {
          addLog(`Ëé∑ÂèñÊí≠ÊîæÂô®Áä∂ÊÄÅÂ§±Ë¥•: ${e.message}`, 'warning')
        }
      } else {
        addLog(`Video.jsÊí≠ÊîæÂô®Áä∂ÊÄÅ: Êú™ÂàùÂßãÂåñ`, 'warning')
      }
    }

    // ÊµãËØïËßÜÈ¢ëURL
    const testVideoUrl = async () => {
      if (!event.value || !currentVideo.value) {
        addLog('Ê≤°ÊúâÂèØÊµãËØïÁöÑËßÜÈ¢ë', 'warning')
        return
      }

      const videoUrl = getVideoUrl(event.value.id, currentVideo.value.fileName)
      addLog(`ÊµãËØïËßÜÈ¢ëURL: ${videoUrl}`, 'info')
      
      try {
        const response = await fetch(videoUrl, { method: 'HEAD' })
        if (response.ok) {
          addLog(`URLÊµãËØïÊàêÂäü - Áä∂ÊÄÅÁ†Å: ${response.status}`, 'success')
          addLog(`ÂÜÖÂÆπÁ±ªÂûã: ${response.headers.get('content-type')}`, 'info')
          addLog(`ÂÜÖÂÆπÈïøÂ∫¶: ${response.headers.get('content-length')} bytes`, 'info')
        } else {
          addLog(`URLÊµãËØïÂ§±Ë¥• - Áä∂ÊÄÅÁ†Å: ${response.status}`, 'error')
        }
      } catch (err) {
        addLog(`URLÊµãËØïÈîôËØØ: ${err.message}`, 'error')
      }
    }



    // ÂàáÊç¢ËßÜÈ¢ë
    const switchVideo = async (index) => {
      if (index < 0 || index >= videoList.value.length || index === currentVideoIndex.value) {
        return
      }
      
      addLog(`ÂàáÊç¢Âà∞ËßÜÈ¢ë ${index + 1}/${videoList.value.length}`, 'info')
      currentVideoIndex.value = index
      currentVideo.value = videoList.value[index]
      addLog(`Êñ∞ËßÜÈ¢ëÊñá‰ª∂: ${currentVideo.value.fileName}`, 'info')
      
      // Êõ¥Êñ∞Êí≠ÊîæÂô®Ê∫ê
      if (player) {
        const videoUrl = getVideoUrl(event.value.id, currentVideo.value.fileName)
        addLog(`Êõ¥Êñ∞Êí≠ÊîæÂô®Ê∫ê: ${videoUrl}`, 'info')
        
        // Ë•øÁìúÊí≠ÊîæÂô®ÂàáÊç¢ËßÜÈ¢ëÊ∫ê
        player.src = videoUrl
        player.play()
      }
      
      // Êõ¥Êñ∞URL‰ΩÜ‰∏çÈáçÊñ∞Âä†ËΩΩÈ°µÈù¢
      const newPath = `/video-player/${event.value.id}/${index}`
      if (route.path !== newPath) {
        router.replace(newPath)
      }
    }

    // Ê†ºÂºèÂåñÊó•Êúü
    const formatDate = (date) => {
      return dayjs(date).format('YYYYÂπ¥MMÊúàDDÊó•')
    }

    // Ê†ºÂºèÂåñËßÜÈ¢ëÊó∂Èïø
    const formatDuration = (seconds) => {
      const mins = Math.floor(seconds / 60)
      const secs = Math.floor(seconds % 60)
      return `${mins}:${secs.toString().padStart(2, '0')}`
    }

    // ËøîÂõû‰∏ä‰∏ÄÈ°µ
    const goBack = () => {
      router.back()
    }

    // ÁõëÂê¨ currentVideo ÂèòÂåñÔºåÁ°Æ‰øù DOM ÂÖÉÁ¥†ÂèØÁî®ÂêéÂÜçÂàùÂßãÂåñÊí≠ÊîæÂô®
    watch([currentVideo, () => videoPlayer.value], async ([newVideo, newVideoElement], [oldVideo]) => {
      if (newVideo && newVideoElement && newVideo !== oldVideo) {
        addLog('Ê£ÄÊµãÂà∞currentVideoÂèòÂåñÔºåÈáçÊñ∞ÂàùÂßãÂåñÊí≠ÊîæÂô®', 'info')
        await nextTick()
        await initializePlayer()
      }
    }, { flush: 'post' })

    // ÁªÑ‰ª∂ÊåÇËΩΩ
    onMounted(async () => {
      addLog('VideoPlayerÁªÑ‰ª∂Â∑≤ÊåÇËΩΩ', 'info')
      addLog(`ÂΩìÂâçÁéØÂ¢É: ${process.env.NODE_ENV}`, 'info')
      addLog(`Áî®Êà∑‰ª£ÁêÜ: ${navigator.userAgent}`, 'info')
      
      // Ê£ÄÊü•Ë•øÁìúÊí≠ÊîæÂô®ÊòØÂê¶ÂèØÁî®
      if (typeof Player === 'undefined') {
        addLog('Ë•øÁìúÊí≠ÊîæÂô®Êú™Ê≠£Á°ÆÂä†ËΩΩ', 'error')
      } else {
        addLog(`Ë•øÁìúÊí≠ÊîæÂô®Â∑≤Âä†ËΩΩ`, 'success')
      }
      
      loadVideoData()
    })

    // ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÁêÜ
    onBeforeUnmount(() => {
      if (player) {
        player.destroy()
        player = null
      }
    })

    return {
      event,
      currentVideo,
      currentVideoIndex,
      videoList,
      loading,
      error,
      videoPlayer,
      videoContainer,
      debugContent,
      showDebugLog,
      debugLogs,
      loadVideoData,
      switchVideo,
      testVideoUrl,
      checkDOMState,
      clearLog,
      formatDate,
      formatDuration,
      goBack,
      getMediaUrl,
      getVideoUrl
    }
  }
}
</script>

